.tip-div{
    display: flex;
    flex-flow: column wrap;
    justify-content: center;
    max-width: 290px;
    margin: auto;
    padding-bottom:30px;
  }
  
  #tip-text{
      max-width: 290px;
      margin: auto;
      margin-left: 0px;
      font-size: 17px; 
      color: #F7F6F2;
      padding-top: 0px;
      letter-spacing: 1px;
      padding-top: 10px;
      padding-bottom: 10px;
      padding-left: 0px;
  }
  
  #coffee-button {
      width: 150px;
      border: 2px solid black;
      border-radius: 12px;
      cursor: pointer;
  }

  @media (min-width: 425px){

    .tip-div{
        max-width: 734px;
        margin: auto;
        padding-bottom:30px;
        padding-left: 30px;
      }

      #tip-text{
        max-width: 375px;
        padding-top: 10px;
        padding-bottom: 10px;
    }

    #coffee-button {
        margin: 0px;
    }


  
}


@media (min-width: 768px){

    
    .tip-div{
        max-width: 460px;
        margin: auto;
        padding-bottom:30px;
        padding-left: 10px;
        justify-content: flex-start;
      }

      #tip-text{
        max-width: 375px;
        padding-top: 10px;
        padding-bottom: 10px;
        margin-left: 20px;
    }

    #coffee-button {
        margin: 10px;
        margin-left: 20px;
    }



}

@media (min-width: 1024px){
    .tip-div{
        max-width: 680px;
        margin: auto;
        padding-bottom:30px;
        padding-left: 20px;
      }

      #tip-text{
        max-width: 375px;
        padding-top: 10px;
        padding-bottom: 10px;
        
    }

    #coffee-button {
        margin: 20px;
    }



}

/* now*/
@media (min-width: 1440px){
    .tip-div{
        max-width: 680px;
        margin: auto;
        padding-bottom:30px;
        padding-left: 0px;
      }

      #tip-text{
        max-width: 375px;
        padding-top: 10px;
        padding-bottom: 10px;
        margin-left: 10px;
    }

    #coffee-button {
        margin: 10px;
        margin-left:5px;
    }



}
  
  